@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,700;1,300;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav{font-family:"Josefin Sans",sans-serif}.nav nav{display:flex;justify-content:space-around;align-items:center;min-height:10vh;background:#DB75C5;color:#fff}.nav nav .logo{width:64px}.nav nav .nav-links{width:40%;display:flex;justify-content:space-around;align-items:center;list-style:none}a{text-decoration:none;letter-spacing:3px;text-transform:uppercase}nav li:hover{text-decoration:none;color:#6A1051}.footer{width:100%;min-height:4vh;margin-top:auto;display:flex;justify-content:space-around;align-items:center;background:#DB75C5}.footer .impressum{display:flex;flex-direction:column;align-items:center;padding:12px;font-size:12px;list-style:none;color:#fff}.container{width:1280px;height:100vh;margin:auto;background:#FFF5DB}*{box-sizing:border-box;margin:0;padding:0}body{background:#fff}

